@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{border-bottom:solid 1px #0096c7;background-color:#023e8a;box-shadow:0 1px 2px rgba(0,0,0,.15);height:73px;width:100%}.header img{width:45x;height:45px;align-items:center;margin:auto}.header nav{height:100%;max-width:992px;align-items:center;margin:auto;padding:1rem}.header nav h2{color:#000;position:relative;top:-12px;left:10px;font-size:30px;letter-spacing:1px}
.tasks{background-color:#ffefd5;height:85vh;width:calc(922px - 266px);padding:74px 20px 0}.tasks .add{cursor:pointer}.tasks .add .plus{color:#dd4b39;font-size:18px}.tasks .add .addtask{color:#545454;font-size:14px;margin-left:15px}.tasks .dialog{margin-top:10px}.tasks .dialog input{width:100%;border:1px solid #ddd;background-color:#fff;border-radius:3px;height:35px;padding-left:10px;padding-right:10px}.tasks .dialog .actions{margin-top:10px;display:flex;justify-content:space-between}.tasks .dialog .actions .addbtn{background-color:#db4c13;color:#fff;border:1px solid transparent;margin-right:5px;font-weight:bold;font-size:13px;padding:6px 12px 7px 12px;border-radius:3px !important}.tasks .dialog .actions .addbtn:disabled{background-color:gray}.tasks .dialog .actions .addbtn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.tasks .dialog .actions .cancelbtn{all:unset;color:#555;cursor:pointer;font-size:14px;margin:0 5px}.tasks .taskItemContainer{margin-top:40px;padding:0 20px}.tasks .taskItemContainer .task-item{display:flex;justify-content:space-between}.tasks .taskItemContainer .task-item:not(:last-child){border-bottom:1px solid #db4c3f}.tasks .taskItemContainer em{cursor:pointer;margin-left:5px;top:3px;position:relative}
.sidebar{-webkit-user-select:none;-ms-user-select:none;user-select:none;width:266px;height:85vh;padding-top:74px;border-right:1px solid #f1f1f1;background-color:#0096c7}.sidebar div{margin-bottom:10px;cursor:pointer;padding:15px}.sidebar div.active{background-color:#fff}.sidebar div .icon{margin-right:10px}

.content {
    display: flex;
    max-width: 992px;
    margin: auto;
    background-color:#0096c7;
  }
  
  
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:0;padding:0;line-height:1;font-family:"Roboto",sans-serif;color:#000;background-color:#0096c7}
